<template>
  <div id="app" class="container">
    <img src="./assets/logo.png" class="logo">
  <todo-list/>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld'
import TodoList from './components/TodoList'

export default {
  name: 'App',
  components: {
    HelloWorld,
    TodoList,
  }
}
</script>

<style>
body{
  background-color: black;
}

*{
  box-sizing: border-box;
}

.container{
  max-width: 600px;
  margin: 0 auto;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #fafafa;
  margin-top: 60px;
  font-size: 24px;
  background-color: rgb(241, 220, 220);
  border-radius: 20px;
  padding: 50px;
  border: 6px solid burlywood;
  
}

.logo{
  display: block;
    margin: 20px auto;
    height: 80px;
    border-radius: 109px;
    width: 16%;
}
.logo{
	-webkit-animation: rotate-vert-center 0.9s cubic-bezier(0.455, 0.030, 0.515, 0.955) infinite both;
	        animation: rotate-vert-center 0.9s cubic-bezier(0.455, 0.030, 0.515, 0.955) infinite both;
}


@-webkit-keyframes rotate-vert-center {
  0% {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
  }
  100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}
@keyframes rotate-vert-center {
  0% {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
  }
  100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}

</style>



