<template>
  <div>
      <input type="text" class="todo-input" placeholder="What needs to be done" v-model="newTodo" @keyup.enter= "addTodo">

      <transition-group name="fade" enter-active-class="animated fadeInUp" leave-active-class="animated fadeOutDown">

      <div v-for="( todo , index )  in todosFiltered" :key="todo.id" class="todo-item">
          <div class="todo-item-left">
              <input type="checkbox" v-model="todo.completed">
           <div v-if="!todo.editing" @dblclick="editTodo(todo)" class="todo-item-label" :class="{completed :todo.completed}"> {{ todo.title }}</div>
           <input v-else class="todo-item-edit" type="text" v-model="todo.title" @blur="doneEdit(todo)" @keyup.enter="doneEdit(todo)" @keyup.esc="canselEdit(todo)" v-focus>
          </div>
          <div class="remove-item" @click="removeTodo(index)">
              &times;
          </div>
      </div> 
      </transition-group>  

      <div class="extra-container">
          <div> <label class="knap-T"> <input type="checkbox" :checked="!anyRemainig" @change="checkAllTodos">check All</label></div>
          <div class="knap-T">{{remaining}}items left </div>
      </div>


      <div class="extra-container">
          <div>
              <button :class="{active:filter == 'all'}" @click="filter='all'"> ALL</button>
              <button :class="{ active: filter == 'active'}"
              @click="filter = 'active'"> Active </button>
              <button :class="{active: filter == 'completed'}"
              @click="filter = 'completed'">completed</button>
          </div>
          <div>
              <transition name="fade">
             <button v-if="showClearCompletedButton" @click="clearCompleted"> Clear complete</button> 
              </transition>
          </div> 
      </div>    
  </div>
</template>

<script>
export default {
  name: "todo-list",
  data() {
    return {
      newTodo: "",
      idForTodo: 3,
      beforEditCache: "",
      filter: 'all',
      todos: [
        {
          id: 1,
          title: "Clean the Home",
          completed: false,
          editing: false
        },
        {
          id: 2,
          title: "shopping at CityGross",
          completed: false,
          editing: false
        }
      ]
    };
  },

  computed:{
    remaining(){
        return this.todos.filter(todo =>!todo.completed).length 
    },
    anyRemainig(){
        return this.remaining != 0
    },
    todosFiltered(){
        if (this.filter == 'all'){
            return this.todos
        }else if (this.filter == 'active') {
            return this.todos.filter(todo => !todo.completed)
        }else if (this.filter == 'completed') {
            return this.todos.filter(todo => todo.completed)

        }
        return this.todos
    },
    showClearCompletedButton(){
        return this.todos.filter(todo => todo.completed).length > 0
    }
  },

  directives: {
    focus: {
      inserted: function(el) {
        el.focus();
      }
    }
  },

  methods: {
    addTodo() {
      if (this.newTodo.trim().length == 0) {
        return;
      }

      this.todos.push({
        id: this.idForTodo,
        title: this.newTodo,
        completed: false
      });
      this.newTodo = "";
      this.idForTodo++;
    },
    editTodo(todo) {
      this.beforEditCache = todo.title;
      todo.editing = true;
    },
    doneEdit(todo) {
      if (todo.title.trim().length == "") {
        todo.title = this.beforEditCache;
        return;
      }
      todo.editing = false;
    },

    canselEdit(todo) {
      todo.title = this.beforEditCache;
      todo.editing = false;
    },
    removeTodo(index) {
      this.todos.splice(index, 1);
    },
    checkAllTodos (){
          this.todos.forEach((todo) => todo.completed = event.target.checked)
    },
    clearCompleted(){
        this.todos = this.todos.filter (todo => !todo.completed)
    }
  }
};
</script>

<style lang= "scss">
    @import url("https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.5.2/animate.min.css");

.todo-input {
  width: 100%;
  padding: 10px 18px;
  font-size: 18px;
  margin-bottom: 16px;

  &:focus {
    outline: 0;
  }
}

.todo-item {
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  animation-duration: 0.3s;
}

.remove-item {
  cursor: pointer;
  margin-left: 14px;
  color: black;
}
.remove-item:hover {
  color: rgb(250, 3, 3);
}
.todo-item-left {
  display: flex;
  align-items: center;
}
.todo-item-label {
    padding: 10px;
    border: 1px solid black;
    margin-left: 12px;
    background-color: white;
    color: burlywood;
    border-radius: 15px;
}
.todo-item-edit {
  font-size: 24px;
  color: #2c3e50;
  margin-left: 12px;
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  font-family: "Avenir", Arial, Helvetica, sans-serif;
}
.todo-item-edit:focus {
  outline: none;
}

.completed {
  text-decoration: line-through;
  color: grey;
}

.extra-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    border-top: 1px solid #c61919;
    padding-top: 14px;
    margin-bottom: 14px;
}

// button{
//     font-size: 14px;
//     background-color: white;
//     appearance: none;
// }
// button:hover{
//     background: lightgreen;
// }
// button:focus{
//     outline: none;
// }

// .active{
//     background: lightgreen;
// }
.active , button {
  display: inline-block;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  position: relative;
  cursor: pointer;
  top: 0;
  padding: 0 22px;
  border: 4px solid rgb(255,255,255);
  font: normal normal bold 10px/25px Arial, Helvetica, sans-serif;
  color: rgb(222, 184, 135);
  text-align: center;
  text-transform: uppercase;
  -o-text-overflow: clip;
  text-overflow: clip;
  background: #ffffff;
}

.active , button:hover {
  color: #FFAE00;
  background: #000000;
}

.fade-enter-active, .fade-leave-to-active{
    transition: opacity .2s;
}

.fade-enter, .fade-leave-to {
    opacity: 0;
}

.knap-T{
  color: #2c3e50;
}

</style>



